.createpost {
    display:flex;
    flex-direction: column;
    margin: 5px;
    padding: 5px;
    border-bottom: solid 1px rgba(255,255,255,.25);
    gap: 15px;
}

.createpost__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.createpost__image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 360px;
}

.createpost__message {
	padding: 5px;
    color: white;
    border-radius: 15px;
	font-family: Tahoma, sans-serif;
	background-position: bottom right;
	background-repeat: no-repeat;
    background-color: rgb(37, 37, 37);
    width: 250px;
    height: 100px;
    resize: none;
}

.createpost__bottom {
    display: flex;
    justify-content: space-between;
}

.createpost__button {
    text-transform: capitalize !important;
}

@media only screen and (max-width: 700px) {
    .createpost__image {
        width: 50px;
        height: 50px;
    }

    .createpost__message {
        padding: 5px;
        color: white;
        font-family: Tahoma, sans-serif;
        background-position: bottom right;
        background-repeat: no-repeat;
        background-color: rgb(37, 37, 37);
        width: 150px;
        height: 50px;
        resize: none;
    }

    .createpost__button {
        width: 55px;
        height: 30px;
        font-size: 10px;
    }
}